            }
        },
        computed: {
            applist: function () {
                return this.apps
                    .filter(app => app.title.includes(this.getSearch()))
                    .filter(app => Math.round(app.rating) >= this.getMinimumRating())
                    .filter(app => {
                        if (this.getTags().length == 0) {
                            return true;
                        }
                        return this.getTags().some(oneTag => app.tags.includes(oneTag))
                    });
            }
        }
    }
